/*弹窗*/

.wu-dialog {
	opacity: 0;
	-webkit-transition: all .4s;
	transition: all .4s;
	position: relative;
	z-index: 999;
}

.dialog-content {
	width: 280px;
	position: fixed;
	top: 50%;
	left: 50%;
	background-color: #fff;
	z-index: 999;
	text-align: center;
	border-radius: 4px;
	overflow: hidden;
	-webkit-transform: translate3d(-50%,-50%,0) scale(.8);
